import React from 'react';
import MeReport from './Components/MeReport';
import NotFound from './Components/NotFound';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import './App.css';

function App() {
  return (
    <div className="App">
      <Router>
        <Router path="/" component={NotFound} />
        <Route path="/:userId" exact component={MeReport} />
      </Router>
    </div>
  );
}

export default App;
